.about {
  text-align: left;
  font-family: "Aquatico";

  margin:0 30px;

  @media screen and (max-width: 800px) {
    padding: 30% 2rem 10%;
  }
  header {
    height: 100vh;
    background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url("/images/about.jpg");
    background-attachment: fixed;

  }

  .branding {
    flex-direction: row;
  }
}

